body{
    padding: 0;
    margin: 0;
}

/* Navbar */
.navbar{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    /* background-color: #333; */
}

.navbar .wrap-nav{
    display: flex;
    /* background-color: red; */
    height: 50px;
    width: 880px;
    margin-top: 35px;
    margin-left: 50px;
    justify-content: space-between;
}

.navbar .wrap-nav .nav-menu{
    flex-direction: row;
    display: flex;
    list-style: none;
}

.navbar .wrap-nav .nav-menu a{
    text-decoration: none;
    padding: 0 20px;
    font-weight: bold;
    color: gray;
    display: flex;
    font-family: Arial, Helvetica, sans-serif;
}

.navbar .wrap-nav .nav-menu a:hover{
    font-weight: bolder;
    color: black;
}

.navbar .wrap-nav img{
    width: 130px;
    height: 33px;
    margin-top: -10px;
}

.navbar .wrap-navbar{
    display: flex;
    /* background-color: red; */
    height: 50px;
    width:350px;
    margin-top: 35px;
    margin-right: 20px;
}

.navbar .wrap-navbar .navbar-menu{
    flex-direction: row;
    display: flex;
    list-style: none;
}

.navbar .wrap-navbar .navbar-menu a{
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    justify-content: space-between;
    text-decoration: none;
    color: black;
    padding: 20px;
    margin-top: -20px;
}
.navbar .wrap-navbar .navbar-menu button{
    border-radius: 20px;
    border: 1px solid #1e90ff;
    padding-left: 40px;
    padding-right: 40px;
    height: 40px;
    margin-top: -11px;
    background-color: white;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 15px;
    cursor: pointer;
}

/* main */
.cont-main{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.cont-main .wrap-main{
    /* background-color: red; */
    width: 1300px;
    height: 500px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.cont-main .wrap-main .main h1{
    font-size: 60px;
    font-family: Arial, Helvetica, sans-serif;
}
.cont-main .wrap-main .main p{
    font-family: Arial, Helvetica, sans-serif;
    margin-bottom: 90px;
    margin-top: -30px;
}

.cont-main .wrap-main .main span{
    font-weight: bolder;
}

.cont-main .wrap-main .main button{
    padding: 20px;
    padding-left: 50px;
    padding-right: 50px;
    border-radius: 50px;
    border: none;
    font-size: 16px;
    font-weight: bolder;
    font-family: Arial, Helvetica, sans-serif;
    background-color: #246bc9;
    color: white;
    box-shadow: 60px 18px 80px #246bc980;
}

/* about */
.cont-about{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.cont-about .wrap-about{
        /* background-color: red; */
        width: 900px;
        height: 600px;
        justify-content: center;
        align-items: center;
}

.cont-about .wrap-about .about{
    margin-top: 50px;
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    justify-content: center;
    text-align: center;
    flex-direction: column;
}

.cont-about .wrap-about .about h1{
    font-weight: bolder;
    margin-bottom: -10px;
}

.cont-about .about-menu{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}

.cont-about .about-menu .wrap-am{
    background-color: white;
    width: 250px;
    height: 260px;
    border: 2px solid rgba(150, 150, 150, 0.384);
    border-radius: 20px;
    cursor: pointer;
}

.cont-about .about-menu .wrap-am:hover{
    border: 2px solid #246bc9;
}

.cont-about .wrap-allmenu{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-top: 50px;
}

.cont-about .wrap-about .about .button-about{
    padding: 13px;
    padding-left: 35px;
    padding-right: 35px;
    border-radius: 30px;
    border: 1px solid #246bc9;
    color: #246bc9;
    font-weight: bold;
    font-family: Arial, Helvetica, sans-serif;
    cursor: pointer;
    background-color: white;
}

.cont-about .about-menu .about-allmenu{
    margin-top: 45px;
    margin-left: 20px;
    font-family: Arial, Helvetica, sans-serif;
}

.cont-about .about-menu .about-allmenu img{
    width: 60px;
}

.cont-about .about-menu .about-allmenu p{
    font-family: Arial, Helvetica, sans-serif;
    font-size: 14px;
}